@import '_styles/variables';
@import '_styles/mixins';

.HomeRoute {

  &__banner {
    position: relative;
    padding: calc(12px + 2vw) 0;
    background: transparent url('../_images/misc/bannerPattern.png') repeat;
  }

  &__bannerGrid {
    grid-auto-flow: row;

    @include respondAbove(s) {
      grid-auto-flow: column;
    }
  }

  &__bannerText {
    align-self: center;

    h1 {
      margin: 0 0 20px;
    }

    p {
      margin-bottom: 0;

      @include respondAbove(s) {
        margin-bottom: 5vw;
      }
    }
  }
}
